import { Product, ProductItem } from "@prisma/client";


/**
 * Функция отображения минимальной цены товара
 * @param items -массив товаров
 * @returns 
 */
export const calcMinProductPrice = (products: any): number => {
    const productsItemsPrice = products
                                    .map((product) => product.items
                                    .map((item) => item.price))
                                    .map((product) => Math.min(...product));
    return productsItemsPrice;
};